//
// mixins
//

@import "flex_mixins";
@import "grid_mixins";

:root {
    --black:                    #{map-get($colors, "black")};
    --white:                    #{map-get($colors, "white")};
    --gray:                     #{map-get($colors, "gray")};
    --gray-dark:                #{map-get($colors, "gray-dark")};
    --blue:                     #{map-get($colors, "blue")};
    --indigo:                   #{map-get($colors, "indigo")};
    --purple:                   #{map-get($colors, "purple")};
    --pink:                     #{map-get($colors, "pink")};
    --red:                      #{map-get($colors, "red")};
    --orange:                   #{map-get($colors, "orange")};
    --yellow:                   #{map-get($colors, "yellow")};
    --green:                    #{map-get($colors, "green")};
    --teal:                     #{map-get($colors, "teal")};
    --cyan:                     #{map-get($colors, "cyan")};

    --primary:                  #{map-get($theme-colors, "primary")};
    --secondary:                #{map-get($theme-colors, "secondary")};
    --success:                  #{map-get($theme-colors, "success")};
    --info:                     #{map-get($theme-colors, "info")};
    --warning:                  #{map-get($theme-colors, "warning")};
    --alert:                    #{map-get($theme-colors, "alert")};
    --light:                    #{map-get($theme-colors, "light")};
    --dark:                     #{map-get($theme-colors, "dark")};

    --body-color:               #{$body-color};
    --body-bg:                  #{$body-bg};

    --link-color:               #{$link-color};
    --link-hover-color:         #{$link-hover-color};

    --gutter:                   #{map-get($spacers, "gutter")};
    --gutter-xs:                #{map-get($spacers, "gutter-xs")};
    --gap:                      #{map-get($spacers, "gap")};
    --gap-xs:                   #{map-get($spacers, "gap-xs")};

    --line-height-header:       $line-height-header;
    --line-height-paragraph:    $line-height-paragraph;
}

::placeholder {
    color: $gray-600;
}

@include mq-min(desktop) {
    ::-webkit-scrollbar-track {
        background-color: $gray-200;
    }

    ::-webkit-scrollbar-thumb {
        background-color: $gray-500;
    }

    ::-webkit-scrollbar {
        @include box($scrollbar-w, $scrollbar-h);
    }
}

html {
    font-family: sans-serif;
    font-variant-ligatures: no-common-ligatures;
    -webkit-text-size-adjust: 100%;
    text-size-adjust: 100%;
    -webkit-touch-callout: none;
    scroll-behavior: smooth;
}

body {
    background: $body-bg;
    font-size: $body-size;
    font-weight: 400;
}

:is(input, textarea, button, main) {
    &:focus {
        outline: none;
    }
}

a, area, button, [role='button'], input, label, select, summary, textarea {
    touch-action: manipulation;
}

* > p:last-child {
    margin-bottom: 0;
}

:is(h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6) {
    font-weight: 700;

    a {
        display: block;
    }

    p {
        color: currentColor;
        line-height: inherit;
        margin-bottom: 0;
    }
}

a {
    display: inline-block;

    :where(h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6) {
        font-size: inherit;
        color: currentColor;
    }
}

img {
    display: inline-block;
    height: auto;
    max-height: 100%;
    max-width: 100%;
    width: auto;

    &:where(.aligncenter, .align-center) {
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
}

[type="submit"], [type="reset"], [type="button"] {
    cursor: pointer;
}

figure {
    &:where(.wp-caption, .caption) {
        max-width: 100% !important;
        display: block;

        img {
            display: inline-block;
        }

        &:where(.wp-caption-text, .caption-text) {
            padding-top: 10px;
            color: #454545;
        }

        &:where(.aligncenter, .align-center) {
            margin-bottom: 15px;
            margin-left: auto;
            margin-right: auto;
            text-align: center;
        }
    }
}

.screen-reader-text, .submenu-toggle-text, .hidden-text {
    @include hidden-text;
}

.clearfix {
    @include clearfix;
}

[data-glyph]::before {
    content: attr(data-glyph);
    font-family: $awesome-font;
}

[data-glyph-after]::after {
    content: attr(data-glyph-after);
    font-family: $awesome-font;
}

/** @todo flexbox & grid */
.flex-x {
    @include flex-x();

    & > :where(.auto, .shrink) {
        width: auto;
    }

    @include mq-min(tablet) {
        & > :where(.t-auto, .t-shrink) {
            width: auto;
        }
    }

    @include mq-min(desktop) {
        & > :where(.d-auto, .d-shrink) {
            width: auto;
        }
    }
}

.flex-y {
    @include flex-y;

    & > .cell {
        height: auto;
        max-height: none;
    }
}

.grid-x {
    @include grid-x;
}

.grid-y {
    @include grid-y;
}

.cell {
    @include cell();

    &.auto {
        @include flex-auto;
    }

    &.shrink {
        @include flex-shrink;
    }

    @include mq-min(tablet) {
        &.t-auto {
            @include flex-auto;
        }

        &.t-shrink {
            @include flex-shrink;
        }
    }

    @include mq-min(desktop) {
        &.d-auto {
            @include flex-auto;
        }

        &.d-shrink {
            @include flex-shrink;
        }
    }
}

.grid-gap, .flex-gap {
    @include media-gap($gap-xs, $gap, false);
}

.flex-padding-x {
    @include flex-padding-x($gutter-xs, $gutter);
}

.flex-padding-y {
    @include flex-padding-y($gutter-xs, $gutter);
}

// layout container
:is(.layout-container, .flex-container, .grid-container) {
    @include layout-container($gutter-xs, $gutter, false, false);

    &:not(.full) {
        &.xxl {
            @include mq-min(xxl) {
                max-width: map-get($container-max-widths, xxl);
            }
        }

        &.xxxl {
            @include mq-min(xxxl) {
                max-width: map-get($container-max-widths, xxxl);
            }
        }
    }

    &.full {
        max-width: 100%;
        padding-left: 0;
        padding-right: 0;
    }

    &.fluid {
        max-width: 100%;
    }
}

/** @todo layout */
@for $i from 1 through 12 {

    .is-flex {
        &.m-up-#{$i} > .cell, & > .cell.m-#{$i} {
            @include flex-self($i, 1, false);
        }

        &.flex-gap {
            &.m-up-#{$i} > .cell, & > .cell.m-#{$i} {
                @include flex-self($i, 1, $gap-xs);
            }
        }
    }

    .is-grid {
        &.m-up-#{$i} {
            @include grid-template( $i, true );
        }
    }
}

@include mq-min(tablet) {
    @for $i from 1 through 12 {

        .is-flex {
            &.t-up-#{$i} > .cell, & > .cell.t-#{$i} {
                @include flex-self($i, 1, false);
            }

            &.flex-gap {
                &.t-up-#{$i} > .cell, & > .cell.t-#{$i} {
                    @include flex-self($i, 1, $gap);
                }
            }
        }

        .is-grid {
            &.t-up-#{$i} {
                @include grid-template( $i, true );
            }
        }
    }
}

@include mq-min(desktop) {
    @for $i from 1 through 12 {

        .is-flex {
            &.d-up-#{$i} > .cell, & > .cell.d-#{$i} {
                @include flex-self($i, 1, false);
            }

            &.flex-gap {
                &.d-up-#{$i} > .cell, & > .cell.d-#{$i} {
                    @include flex-self($i, 1, $gap);
                }
            }
        }

        .is-grid {
            &.d-up-#{$i} {
                @include grid-template( $i, true );
            }
        }
    }
}

/** @todo responsive viewport */
.overlay {
    position: relative;

    .overlay-bg {
        @include absolute-full;
        background: transparent;
    }
}

.cover {
    display: block;
    overflow: hidden;
    position: relative;

    .cover-bg {
        @include absolute-full;
        background: transparent;
    }
}

.res {
    @include responsive-wrapper();

    &.auto {
        & > img {
            @include responsive-image-auto;
        }
    }

    &.scale {
        @include responsive-scale();
    }

    :where(iframe, embed, object, video) {
        @include absolute-full;
        border: 0;
        width: 100%;
        height: 100%;
    }
}

/** @todo aspect ratio */
.ar-1, .ar-1-1 {
    @include responsive-ratio(1, 1);
}

.ar-3-2 {
    @include responsive-ratio(3, 2);
}

.ar-4-3 {
    @include responsive-ratio(4, 3);
}

.ar-16-9 {
    @include responsive-ratio(16, 9);
}

.ar-2-1 {
    @include responsive-ratio(2, 1);
}

.ar-3-1 {
    @include responsive-ratio(3, 1);
}

/** @todo gutter */
$count: 0;
$count_max: 30;

@while $count <= $count_max {
    .mt-#{$count} {
        margin-top: fs($count) !important;
    }
    .mb-#{$count} {
        margin-bottom: fs($count) !important;
    }
    .pt-#{$count} {
        padding-top: fs($count) !important;
    }
    .pb-#{$count} {
        padding-bottom: fs($count) !important;
    }

    $count: $count + 5;
}

/** @todo font weight */
$fw: 200;
$fw_max: 900;

@while $fw <= $fw_max {
    .fw-#{$fw} {
        font-weight: $fw !important;
    }

    $fw: $fw + 100;
}

/** @todo display */
.hide, .hidden {
    display: none !important;
}

.flex-row {
    flex-direction: row;
}

.flex-column {
    flex-direction: column;
}

/** @todo section */
.section {
    @for $i from 1 through 6 {
        &._h#{$i} {
            margin-bottom: 0;

            .heading-title {
                @include fs-heading(h#{$i});
                line-height: $line-height-header;
            }
        }
    }

    &:not(._full) {
        &._xxl {
            & > .flex-container {
                @include mq-min(xxl) {
                    max-width: map-get($container-max-widths, xxl);
                }
            }
        }

        &._xxxl {
            & > .flex-container {
                @include mq-min(xxxl) {
                    max-width: map-get($container-max-widths, xxxl);
                }
            }
        }
    }

    &._full {
        & > .flex-container {
            max-width: 100%;
            padding-left: 0;
            padding-right: 0;
        }
    }

    &._fluid {
        & > .flex-container {
            max-width: 100%;
        }
    }
}

/** @todo gallery */
[id^=gallery-] {
    $gap: 10;
    @include flex-x;
    @include gap($gap);

    & > .gallery-item {
        @include cell();

        a {
            @include responsive-wrapper();
            @include responsive-ratio(4, 3);
        }
    }

    @for $i from 1 through 9 {
        &.gallery-columns-#{$i} {
            & > .gallery-item {
                @include flex-self($i, 1, $gap);
            }
        }
    }
}

/** @todo .pagination */
.pagination {
    text-align: center;
    border: 0;
    padding: 0;
    margin: 30px 0;
    @include flex-center;

    &::before, &::after {
        @include hidden-text;
    }

    .current {
        font-weight: 500;
        color: $white;
        background-color: $gray-600;
        border: none;
        border-radius: 50%;
        @include box(32);
        display: flex !important;
        align-items: center;
        justify-content: center;
        padding: 0;
        font-size: fs(13);
    }

    li {
        display: block;
        margin-right: 0;

        &:not(:last-child) {
            margin-right: 10px;
        }
    }

    a, button {
        font-weight: 400;
        border: 1px solid $gray-200;
        padding: 0;
        font-size: fs(13);
        position: relative;
        @include box(32, 32, 50%);
        @include flex-center;
        @include hover-color($black, $white);

        i {
            font-style: normal;
            font-weight: 400;
            font-size: fs(14);
        }

        &:hover {
            background-color: $gray-600;
            border: none;

            i {
                color: $white;
            }
        }
    }
}

/** @todo .hotline-mobile */
.hotline-mobile {
    position: fixed;
    background-color: transparent;
    cursor: pointer;
    z-index: 99999;
    backface-visibility: hidden;
    transform: translateZ(1px);
    transition: visibility 0.5s;
    bottom: 60px;
    left: 20px;
    @include box(86);
    opacity: 1;
    animation: fadeIn ease-in-out 8s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;

    .hl-circle {
        @include box(86, 86, 50%);
        background-color: transparent;
        border: 2px solid $red;
        animation: ng-circle-anim 1.2s infinite ease-in-out;
        transition: all 0.5s;
        transform-origin: 50% 50%;
        backface-visibility: hidden;
    }

    .hl-circle-fill {
        @include box(52, 52, 50%);
        top: 17px;
        left: 17px;
        position: absolute;
        border: 2px solid transparent;
        animation: ng-circle-fill-anim 2.3s infinite ease-in-out;
        transition: all 0.5s;
        transform-origin: 50% 50%;
        @include linear-gradient(90deg, $red);
        backface-visibility: hidden;
    }

    .hl-circle-icon {
        @include box(42, 42, 50%);
        animation: ng-circle-icon-anim 1s infinite ease-in-out;
        transform-origin: 50% 50%;
        @include linear-gradient(90deg, $red);
        position: absolute;
        top: 22px;
        left: 22px;
        @include flex-center;
        backface-visibility: hidden;

        &::before {
            font-weight: 900;
            color: $white;
            font-size: fs(22);
            opacity: 0.9;
        }
    }

    .has-tip {
        cursor: default;
        border-bottom: none;
        font-weight: inherit;
    }
}

/** @todo keyframes */
@include keyframes(circle) {
    0% {
        opacity: 1;
    }
    40% {
        opacity: 1;
    }
    100% {
        width: 200%;
        height: 200%;
        opacity: 0;
    }
}

@include keyframes(fadeIn) {
    0% {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@include keyframes(ng-circle-anim) {
    0% {
        transform: rotate(0) scale(0.5) skew(1deg);
        opacity: 0.1;
    }
    30% {
        transform: rotate(0) scale(0.7) skew(1deg);
        opacity: 0.4;
    }
    100% {
        transform: rotate(0) scale(1) skew(1deg);
        opacity: 0.1;
    }
}

@include keyframes(ng-circle-fill-anim) {
    0% {
        transform: rotate(0) scale(0.7) skew(1deg);
        opacity: 0.2;
    }
    50% {
        transform: rotate(0) scale(1) skew(1deg);
        opacity: 0.2;
    }
    100% {
        transform: rotate(0) scale(0.7) skew(1deg);
        opacity: 0.2;
    }
}

@include keyframes(ng-circle-icon-anim) {
    0% {
        transform: rotate(0) scale(1) skew(1deg);
    }
    10% {
        transform: rotate(-25deg) scale(1) skew(1deg);
    }
    20% {
        transform: rotate(25deg) scale(1) skew(1deg);
    }
    30% {
        transform: rotate(-25deg) scale(1) skew(1deg);
    }
    40% {
        transform: rotate(25deg) scale(1) skew(1deg);
    }
    50% {
        transform: rotate(0) scale(1) skew(1deg);
    }
    100% {
        transform: rotate(0) scale(1) skew(1deg);
    }
}

/** @todo .up-seo-list */
.up-seo-list {
    list-style: none;
    margin: 0 1% 30px 1%;
    color: $body-color;

    li {
        padding-bottom: 8px;
        padding-top: 8px;
        display: flex;
        align-items: baseline;

        &:not(:last-child) {
            border-bottom: 1px solid rgba(0, 0, 0, 0.05);
        }

        .txt {
            .post-title {
                font-size: fs(14);
                @include hover-color(currentColor, $black);
            }
        }
    }
}

/** @todo .toTop */
.toTop {
    @include box(32);
    font-size: fs(17);
    position: fixed;
    right: 10px;
    bottom: 5%;
    z-index: 9998;
    transition: all 0.3s ease-in-out;
    line-height: 32px;
    @include flex-center;
    background-color: rgb(255 255 255 / 80%);
    backdrop-filter: blur(5px);

    @include mq-max(tablet) {
        bottom: 10px;
    }

    &::before {
        font-weight: 300;
        @include box(30);
        @include flex-center;
        color: $black;
    }

    &::after {
        content: "";
        @include absolute-full;
        border: 1px solid #eee;
        outline: 1px solid #9a9a9a;
        background-color: transparentize($body-bg, 0.5);
    }

    & > #toTopHover {
        display: none;
    }

    &:hover, &:focus {
        color: $primary;

        &::after {
            outline: 1px solid $gray-600;
            background-color: transparentize($gray-300, 0.5);
        }
    }
}

/** @todo .inline-share */
.inline-share {
    border: 1px solid rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: baseline;
    font-size: fs(12);
    @include media-mt(20, false, 30);
    @include media-mb(20, false, 30);
    @include media-padding(10, false, 15);

    .title {
        @include flex-align-center;
        font-size: inherit;
        font-weight: 500;
        margin-right: 15px;
        position: relative;
        top: 3px;

        &::before {
            height: auto;
            margin-right: 10px;
            font-size: fs(14);
            font-weight: 300;
        }
    }

    .inline-share-groups {
        align-items: flex-end;
        display: flex;

        & > * {
            margin-left: 10px;
        }

        .zalo-follow-only-button {
            max-height: 20px;
        }
    }
}

