/** @todo .sharing-toolbox */
.sharing-toolbox {
    .share-title {
        bottom: 30px;
        color: $body-color;
        @include flex-align-center;
        font-size: 12px;
        font-weight: 400;
        position: relative;
        transform: rotate(-90deg);
        white-space: nowrap;

        &::before {
            background: #ccc;
            content: "";
            height: 1px;
            margin-right: 3px;
            width: 10px;
        }
    }

    .social-share {
        & > .share-label {
            display: none;
        }

        ul.share-actions {
            display: flex;
            flex-direction: column;
            @include list-reset();

            & > li.share-action {
                display: flex;
                margin-bottom: 15px;

                & > button.share-button {
                    cursor: pointer;
                    opacity: 0.8;
                    transition: 0.3s;

                    &:hover {
                        opacity: 1;
                    }
                }
            }
        }
    }
}

/** @todo off-canvas */
.position-top {
    transform: translateY(-100%);
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
}

.position-bottom {
    transform: translateY(100%);
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
}

.js-off-canvas-overlay {
    background-color: transparentize($black, 0.5);
    transition: opacity 0.2s ease-in-out, z-index 0.2s ease-in-out;

    &.is-visible {
        z-index: 10000 !important;
        transform: translate3d(0, 0, 0);
    }
}

.menu-lines {
    background: none;
    border: none;
    cursor: pointer;
    display: block;
    outline: none;
    padding: 10px;
    position: relative;
    transition: color 0.3s ease;

    .menu-txt {
        font-size: 14px;
        font-weight: 500;
        position: absolute;
        right: 40px;
        top: 50%;
        transform: translateY(-50%);
        transition: 0.3s ease-in-out;
    }

    .hamburger {
        @include hamburger($body-color, $gray-900, 26px, 22px, 2px, 3);
    }
}

.off-canvas {
    background-color: transparentize($black, 0.2);

    &.is-open {
        transform: translate(0) translateZ(3px);
    }

    ul.vertical.menu {
        position: relative;
        z-index: 9998;
        height: 100%;
        overflow: hidden;
        transform: translateZ(3px);
    }

    &.is-transition-overlap {
        z-index: 1000001 !important;

        &.is-closed {
            visibility: hidden;
        }

        &.is-open {
            visibility: visible;
            box-shadow: -3px 0 3px transparentize($body-color, 0.9);
        }
    }

    .menu-lines {
        color: #fff;
        position: absolute;
        top: 16px;
        right: 16px;
        display: block;
        z-index: 9999;
        height: 32px;
        width: 32px;
        opacity: 0;
        transition: .6s 0.5s ease-in-out;
        transform: translateZ(3px);
        padding: 0;

        .menu-txt {
            color: currentColor;
            transition: .6s 0.5s ease-in-out;
        }

        .line {
            position: absolute;
            top: 16px;
            left: 4px;
            height: 1px;
            background-color: currentColor;
            border-radius: 0;
            transition: .6s 0.5s ease-in-out;
            transform: rotate(0deg);
            width: 24px;
            display: block;
        }
    }

    &.is-open {
        .menu-lines {
            opacity: 1;
            transform: translateZ(3px);

            .line-1 {
                transform: rotate(135deg);
            }

            .line-2 {
                transform: rotate(-135deg);
            }
        }
    }

    .title-bar-title {
        font-weight: 500;
        position: absolute;
        top: 16px;
        z-index: 9999;
        transform: translateZ(3px) translateX(-50%);
        left: 50%;

        & > a {
            color: #fff;
            display: block;
            @include fs-heading(h4);

            & > img {
                display: block;
                max-height: 26px;
                width: auto;

                & + span {
                    display: none;
                }
            }

            & > span {
                display: block;
                font-size: 20px;
                font-weight: 500;
            }
        }
    }
}

/** @todo .horizontal-menu */
.horizontal-menu.menu {
    & > li {
        @include flex-align-center;
        position: relative;

        & > a {
            @include flex-align-center;
            line-height: normal;
            padding: 0;
            position: relative;

            &:hover {
                background-color: unset;
            }
        }

        &.is-dropdown-submenu-parent {
            & > a {
                padding-right: 22px;

                &::after {
                    border: solid currentColor;
                    border-width: 0 1px 1px 0;
                    margin-top: -3px;
                    padding: 3px;
                    right: 10px;
                    transform: rotate(45deg) translateY(-2px);
                    width: 6px;
                    font-weight: 300;
                    opacity: 0.6;
                }
            }
        }

        & > .menu {
            &.is-dropdown-submenu {
                top: 100%;
            }
        }

        .menu {
            &.vertical {
                opacity: 0;
                position: absolute;
                visibility: hidden;
            }

            &.is-dropdown-submenu {
                backdrop-filter: blur(15px);
                background-color: rgba(255, 255, 255, 0.9);
                border: none;
                box-shadow: inset 0 0 0 1px transparentize($black, 0.95);
                max-width: unset;
                min-width: 240px;
                width: auto;
                z-index: 999;
                opacity: 0;
            }

            li {
                position: relative;
                transition: all 0.15s linear;

                & > a {
                    background: none;
                    position: relative;
                    @include hover_color($body-color, $gray-900);

                    &:hover {
                        background: none;
                    }
                }

                &.is-active, &.active {
                    & > a {
                        background: none;
                    }
                }
            }
        }

        &:hover, &.is-active, &.active {
            & > a {
                background-color: unset;
            }

            .menu {
                &.is-dropdown-submenu {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
    }

    .is-dropdown-submenu {
        .is-dropdown-submenu-parent {
            &.opens-right {
                & > a {
                    &::after {
                        border: none;
                        @include box(6);
                        border-right: 1px solid currentColor;
                        border-top: 1px solid currentColor;
                        margin-top: 0;
                        right: 24px;
                        top: 50%;
                        transform: translateY(-50%) rotate(45deg);
                    }
                }
            }

            &.opens-left {
                & > a {
                    &::after {
                        border: none;
                        border-right: 1px solid currentColor;
                        border-top: 1px solid currentColor;
                        @include box(6);
                        left: auto;
                        margin-top: 0;
                        right: 14px;
                        top: 50%;
                        transform: translateY(-50%) rotate(225deg);
                    }
                }
            }
        }
    }
}

/** @todo .desktop-menu */
.desktop-menu.menu {
    height: 100%;
    position: static;

    & > li {
        padding: 0;

        &.hide-text, &.hidden-text {
            & > a {
                @include hidden-text;
            }
        }

        &.home-icon {
            position: relative;
            align-items: center;

            & > a {
                @include box(16);
                margin-left: 0;
                margin-right: 10px;
                padding: 0;
                position: relative;

                //@include mq-max(desktop) {
                //    margin-right: 10px;
                //}

                &::after {
                    content: "";
                    @include absolute-full;
                    background-image: inline-svg('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512"><path fill="#{$body-color}" d="M575.8 255.5C575.8 273.5 560.8 287.6 543.8 287.6H511.8L512.5 447.7C512.5 450.5 512.3 453.1 512 455.8V472C512 494.1 494.1 512 472 512H456C454.9 512 453.8 511.1 452.7 511.9C451.3 511.1 449.9 512 448.5 512H392C369.9 512 352 494.1 352 472V384C352 366.3 337.7 352 320 352H256C238.3 352 224 366.3 224 384V472C224 494.1 206.1 512 184 512H128.1C126.6 512 125.1 511.9 123.6 511.8C122.4 511.9 121.2 512 120 512H104C81.91 512 64 494.1 64 472V360C64 359.1 64.03 358.1 64.09 357.2V287.6H32.05C14.02 287.6 0 273.5 0 255.5C0 246.5 3.004 238.5 10.01 231.5L266.4 8.016C273.4 1.002 281.4 0 288.4 0C295.4 0 303.4 2.004 309.5 7.014L564.8 231.5C572.8 238.5 576.9 246.5 575.8 255.5L575.8 255.5z"></path></svg>');
                    background-position: center center;
                    background-repeat: no-repeat;
                    background-size: contain;
                    filter: drop-shadow(1px 1px 2px #9a9a9a);
                }

                &:hover {
                    &::after {
                        background-image: inline-svg('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512"><path fill="#{$gray-900}" d="M575.8 255.5C575.8 273.5 560.8 287.6 543.8 287.6H511.8L512.5 447.7C512.5 450.5 512.3 453.1 512 455.8V472C512 494.1 494.1 512 472 512H456C454.9 512 453.8 511.1 452.7 511.9C451.3 511.1 449.9 512 448.5 512H392C369.9 512 352 494.1 352 472V384C352 366.3 337.7 352 320 352H256C238.3 352 224 366.3 224 384V472C224 494.1 206.1 512 184 512H128.1C126.6 512 125.1 511.9 123.6 511.8C122.4 511.9 121.2 512 120 512H104C81.91 512 64 494.1 64 472V360C64 359.1 64.03 358.1 64.09 357.2V287.6H32.05C14.02 287.6 0 273.5 0 255.5C0 246.5 3.004 238.5 10.01 231.5L266.4 8.016C273.4 1.002 281.4 0 288.4 0C295.4 0 303.4 2.004 309.5 7.014L564.8 231.5C572.8 238.5 576.9 246.5 575.8 255.5L575.8 255.5z"></path></svg>');
                    }
                }
            }

            &.is-active, &.active {
                & > a {
                    &::after {
                        background-image: inline-svg('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512"><path fill="#{$gray-900}" d="M575.8 255.5C575.8 273.5 560.8 287.6 543.8 287.6H511.8L512.5 447.7C512.5 450.5 512.3 453.1 512 455.8V472C512 494.1 494.1 512 472 512H456C454.9 512 453.8 511.1 452.7 511.9C451.3 511.1 449.9 512 448.5 512H392C369.9 512 352 494.1 352 472V384C352 366.3 337.7 352 320 352H256C238.3 352 224 366.3 224 384V472C224 494.1 206.1 512 184 512H128.1C126.6 512 125.1 511.9 123.6 511.8C122.4 511.9 121.2 512 120 512H104C81.91 512 64 494.1 64 472V360C64 359.1 64.03 358.1 64.09 357.2V287.6H32.05C14.02 287.6 0 273.5 0 255.5C0 246.5 3.004 238.5 10.01 231.5L266.4 8.016C273.4 1.002 281.4 0 288.4 0C295.4 0 303.4 2.004 309.5 7.014L564.8 231.5C572.8 238.5 576.9 246.5 575.8 255.5L575.8 255.5z"></path></svg>');
                    }
                }
            }
        }

        & > a {
            font-weight: 400;
            color: $body-color;
            font-size: fs(16);
            padding: 0 15px;
            position: relative;
            transition: all 0.25s ease;

            @include mq-max(desktop) {
                font-size: fs(15);
                padding: 0 10px;
            }

            &::before {
                background-color: rgba(0, 0, 0, 0.1);
                bottom: 5px;
                content: "";
                height: 0;
                left: 15px;
                position: absolute;
                transform: scaleX(0);
                transform-origin: left;
                transition: all 0.3s cubic-bezier(0.345, 0, 0.145, 0.995);
                width: calc(100% - 30px);

                @include mq-max(desktop) {
                    left: 10px;
                    width: calc(100% - 20px);
                }
            }

            &:hover, &:active {
                color: $body-color;

                &::before {
                    transform: scaleX(1);
                }
            }
        }

        &.is-active, &.active {
            & > a {
                color: $body-color;

                &::before {
                    transform: scaleX(1);
                }
            }
        }

        .menu > li {
            position: relative;

            &:first-child {
                padding-top: 5px;
            }

            &:last-child {
                padding-bottom: 5px;
            }

            &:not(:last-child) {
                /*border-bottom: 1px solid rgba(0, 0, 0, 0.05);*/
            }

            & > a {
                font-weight: 400;
                color: $body-color;
                line-height: normal;
                padding-top: 10px;
                padding-bottom: 10px;
                font-size: fs(15);
            }
        }

        &.mega-menu {
            position: static !important;

            & > ul.menu {
                @include flex-x;
                overflow: hidden;
                padding: 0;

                & > li {
                    @include cell();
                    @include media-flex-self(false, 2, 4);
                    padding: 0;

                    & > a {
                        padding: 0 0 10px 0;
                        font-weight: 500 !important;
                        border-bottom: 1px solid rgba(0, 0, 0, 0.05);
                        margin-bottom: 10px;
                        font-size: 16px;
                        @include hover-color($body-color, $body-color);
                    }

                    .menu > li {
                        &:first-child, &:last-child {
                            padding-top: 0;
                            padding-bottom: 0;
                        }

                        &:not(:last-child) {
                            border-bottom: none;
                        }

                        a {
                            padding: 5px 0;
                            font-size: fs(15);
                            @include hover-color($body-color, $body-color);
                        }
                    }

                    & > ul {
                        left: 0;
                        min-width: auto;
                        display: block;
                        position: relative;
                        background: none;
                        backdrop-filter: blur(0);
                        box-shadow: none;
                    }
                }
            }

            &:hover, &.is-active {
                & > ul.menu.is-dropdown-submenu {
                    background-color: rgba(255, 255, 255, 0.9);
                    backdrop-filter: blur(5px);
                    position: absolute;
                    box-shadow: 0 0 6px rgb(0 0 0 / 10%);
                }
            }
        }
    }
}

/** @todo .mobile-menu */
.mobile-menu.menu {
    padding-bottom: 40px;
    padding-top: 0;

    &[data-submenu-toggle="true"] {
        .menu-item {
            & > a {
                &::after {
                    display: none;
                }
            }
        }
    }

    .active > a, a:hover {
        background: none;
        color: #ccc;
    }

    .submenu-toggle {
        border-color: #e6e6e6;
        position: absolute;
        @include box(24);
        right: 10px;
        top: 15px;

        &::after {
            border: solid $white;
            border-width: 0 2px 2px 0;
            padding: 3px;
            position: absolute;
            transform: rotate(45deg) translateY(-7px) translateX(4px);
            width: 7px;
        }

        &[aria-expanded="true"] {
            &::after {
                transform: rotate(225deg) translateY(4px) translateX(-7px);
            }
        }
    }

    & > li {
        position: relative;

        &.lang-item {
            a {
                @include flex-align-center;
                font-size: fs(14);
                font-weight: 300;
                padding-bottom: 0;
                padding-top: 0;

                img, span {
                    display: block;
                }
            }
        }

        & > a {
            color: $white;
            display: block;
            font-size: fs(16);
            font-weight: 500;
            line-height: 1.8;
            margin-bottom: 0;
            letter-spacing: fs(0.5);
            padding: 10px 20px;
        }
    }

    li {
        &.is-accordion-submenu-parent {
            & > a {
                position: relative;

                &::after {
                    font-family: $awesome-font;
                    content: '';
                    position: absolute;
                    right: 20px;
                    font-weight: 300;
                    font-size: 18px;
                }
            }
        }
    }

    .submenu {
        &.nested {
            padding-left: 15px;
            background: none;
        }

        & > li {
            & > a {
                font-size: fs(15);
                font-weight: 300;
                @include hover_color(#eee, #fff);
            }

            &.active {
                a {
                    color: #fff;
                }
            }
        }
    }
}

/** @todo .social-menu */
.social-menu.menu {
    margin: 0;
    @include flex-align-center;
    flex-wrap: nowrap;

    li {
        a {
            @include flex-center;
            padding: 0;

            &::before {
                font-size: fs(15);
            }

            &:hover {
                svg, path {
                    opacity: 0.9;
                }
            }
        }

        svg, path {
            color: #6B6A6A;
            fill: #6B6A6A;
            @include box(22);
        }
    }
}

/** @todo .terms-menu */
.terms-menu.menu {
    padding: 10px 0;

    @include mq-max(desktop) {
        padding: 0;
    }

    & > li {
        @include flex-align-center;
        flex-wrap: nowrap;
        line-height: normal;
        position: relative;

        &:not(:last-child) {
            &::after {
                color: currentColor;
                content: "/";
                margin: 0 5px;
                opacity: 0.8;
                font-size: fs(12);
            }
        }

        & > a {
            color: currentColor;
            display: block;
            line-height: normal;
            padding: 0;
        }
    }
}

/** ... */
ul.reset, ol.reset {
    @include list-reset();

    li {
        & > i, & > svg {
            margin-right: 8px;
        }
    }
}

.is-invalid-input {
    color: $red !important;

    &::placeholder {
        color: $red !important;
    }
}

.wpcf7-form-control-wrap {
    position: relative;

    .wpcf7-not-valid-tip {
        position: absolute;
        font-size: fs(12);
        top: 2px;
        right: 10px;
    }
}

.menu-fallback {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    a {
        @include hover_color($body-color, $body-color);
        @include underline($body-color);
    }
}

.dropdown.menu li {
    & > ul {
        display: none;

        &.js-dropdown-active {
            display: block;
        }
    }
}

.ajax-loading {
    position: relative;

    &::before {
        @include absolute-full;
        z-index: 9998;
        content: '';
        background-color: rgb(0 0 0 / 30%);
    }

    &::after {
        content: '';
        @include centering-self(30);
        z-index: 9999;
        border: 3px solid;
        border-color: $body-color #f3f3f3 $body-color #f3f3f3; // top right bottom left
        border-radius: 50%;
        animation: spin 2s linear infinite;
    }
}

@include keyframes(spin) {
    100% {
        transform: rotate(360deg);
    }
}
